.hidden_cont{
    position: absolute;
    left: -99999px;
    top:0px;
    opacity: 1.0;
    z-index: 0;
}

.uninitialized{
    display: none;
}

.vega-actions a {
    display: none;
}

.vg-tooltip {
    visibility: hidden;
    padding: 6px;
    border-radius: 3px;
    position: fixed;
    z-index: 2000;
    font-family: sans-serif;
    font-size: 11px;
    
    /* The default look of the tooltip is the same as .light-theme
     but it can be overwritten by .dark-theme */
    background-color: rgba(255, 255, 255, 0.9);
    border: 1px solid #d9d9d9;
    color: black;
}
.vg-tooltip td.key, .vg-tooltip td.value {
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.vg-tooltip td.key {
    color: #808080;
    max-width: 150px;
    text-align: right;
    padding-right: 1px;
}
.vg-tooltip td.value {
    max-width: 200px;
    text-align: left;
}

/* Dark and light color themes */
.vg-tooltip.dark-theme {
    background-color: rgba(64, 64, 64, 0.9);
    border: none;
    color: white;
}
.vg-tooltip.dark-theme td.key {
    color: #bfbfbf;
}
.vg-tooltip.light-theme {
    background-color: rgba(255, 255, 255, 0.9);
    border: 1px solid #d9d9d9;
    color: black;
}
.vg-tooltip.light-theme td.key {
    color: #808080;
}



